<template>
  <div class="container">
    <div class="row justify-content-center">
      <div class="col-md-6">
        <h1>Crear Nota</h1>
        <form @submit.prevent="crearNota(nota)">
          <div class="form-group">
            <label for="nombre">Nombre</label>
            <input
              type="text"
              class="form-control"
              placeholder="Ingrese el nombre"
              v-model="nota.nombre"
            />
          </div>
          <div class="form-group">
            <label for="descripcion">Descripcion</label>
            <textarea
              class="form-control"
              cols="30"
              v-model="nota.descripcion"
              rows="2"
              placeholder="Ingrese una descripcion"
            ></textarea>
          </div>
          <div class="form-group mt-3">
            <button type="submit" class="btn btn-primary btn-block">
              GUARDAR
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "Crear",
  data() {
    return {
      nota: {
        nombre: "",
        descripcion: "",
      },
    };
  },
  computed: {
    ...mapState(["token"]),
  },
  methods: {
    crearNota() {
      this.axios
        .post("/notas/crear", this.nota, {
          headers: {
            token: this.token,
          },
        })
        .then((res) => {
          this.$router.push({ name: "Notas" });
          this.nota.nombre = "";
          this.nota.descripcion = "";
        })
        .catch((err) => console.log(err));
    },
  },
};
</script>